/* Global heading styles */
* {
  box-sizing: border-box; }

h1, h2, h3, h4, h5 {
  font-family: "Gilroy-ExtraBold";
  margin: 0;
  padding: 0; }

h1 {
  font-size: 40px;
  line-height: 1.125; }

h2 {
  font-size: 22px;
  line-height: 1.27273; }

h3 {
  font-size: 20px;
  line-height: 1.35; }

h4 {
  font-size: 17px;
  line-height: 1.47059; }

/* Global paragraph styles */
p {
  font-size: 2rem; }

/* Global link styles */
a {
  font-size: 2rem; }

/* Global sup element */
sup {
  font-size: 75%; }

/* Screen reader only */
.sr-only {
  border: 0 none;
  clip: rect(0px, 0px, 0px, 0px);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

/* clearfix */
.row {
  margin-top: -60px;
  margin-left: -15px;
  margin-right: -15px;
  max-width: none; }
  @media (min-width: 768px) {
    .row {
      margin-top: -60px; } }
  @media (min-width: 1200px) {
    .row {
      margin-top: -80px; } }
  .row:after {
    content: " ";
    display: block;
    clear: both; }
  .row + .row,
  .row .row {
    margin-top: 0; }
  .row--reducedWidth {
    margin-left: calc(6.25% - 15px);
    margin-right: calc(6.75% - 15px); }
    @media (min-width: 768px) {
      .row--reducedWidth {
        margin-left: calc(1.96% - 15px);
        margin-right: calc(4.3% - 15px); } }
    @media (min-width: 1520px) {
      .row--reducedWidth {
        max-width: 1470px;
        margin-left: auto;
        margin-right: auto; } }

